/*
	Поделючить mixin:
	@import "../vendor/font-face";

	Подключить нестандартные шрифты:
	@include font-face("DR", "../fonts/Damion Regular", 400, normal, ttf);
*/
/* Base colors */
@font-face {
  font-family: "MP";
  font-style: Normal;
  font-weight: 400;
  src: url("../fonts/MavenPro-Regular.eof") format("eof"), url("../fonts/MavenPro-Regular.ttf") format("truetype"), url("../fonts/MavenPro-Regular.woff") format("woff"), url("../fonts/MavenPro-Regular.woff2") format("woff2"), url("../fonts/MavenPro-Regular.svg#MP") format("svg");
}

@font-face {
  font-family: "MP";
  font-style: Medium;
  font-weight: 500;
  src: url("../fonts/MavenPro-Medium.eof") format("eof"), url("../fonts/MavenPro-Medium.ttf") format("truetype"), url("../fonts/MavenPro-Medium.woff") format("woff"), url("../fonts/MavenPro-Medium.woff2") format("woff2"), url("../fonts/MavenPro-Medium.svg#MP") format("svg");
}

@font-face {
  font-family: "MP";
  font-style: Bold;
  font-weight: 700;
  src: url("../fonts/MavenPro-Bold.eof") format("eof"), url("../fonts/MavenPro-Bold.ttf") format("truetype"), url("../fonts/MavenPro-Bold.woff") format("woff"), url("../fonts/MavenPro-Bold.woff2") format("woff2"), url("../fonts/MavenPro-Bold.svg#MP") format("svg");
}

/* Basic settings */
html {
  font-size: 10px;
}

.page {
  font-family: 'MP', Arial, sans-serif;
  font-weight: 400;
  font-size: 170%;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

button:active, button:focus, input:active, input:focus, textarea:active, textarea:focus, a:active, a:focus {
  outline: none;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.section-content {
  margin: 18rem auto auto;
  width: 50rem;
  text-align: center;
}

.section-content__h3 {
  position: relative;
  display: inline-block;
  margin: 0;
  font-size: 1.4rem;
  font-weight: bold;
}

.section-content__h3:before, .section-content__h3:after {
  position: absolute;
  content: '';
  display: block;
  width: 2.4rem;
  top: 50%;
}

.section-content__h3:before {
  left: -4rem;
}

.section-content__h3:after {
  right: -4rem;
}

.section-content__h1 {
  margin: 4rem 0;
  font-size: 8.6rem;
  font-weight: Medium;
}

.section-content__title {
  font-size: 1.6rem;
  line-height: 2.8rem;
}

/* ===== ANIMATIONS ===== */
@keyframes widthBottomLine {
  from {
    width: 0;
  }
  to {
    width: 90%;
  }
}

.debug-grid {
  height: 100vh;
  position: fixed;
  width: 100%;
  z-index: 1;
}

.debug-grid > div {
  height: inherit;
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.debug-grid > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.debug-grid > div > div > div {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(8.33333% - 20px);
  height: inherit;
  outline: #000 1px solid;
  background: rgba(255, 0, 0, 0.2);
}

.button {
  text-transform: uppercase;
  font-size: 1.6rem;
  padding: 2rem 1.5rem;
  border-radius: 25px;
  background: none;
  cursor: pointer;
  color: inherit;
  transition: all 0.3s ease;
}

.button:hover {
  text-shadow: 1px 1px 2px #000;
}

.button__menu {
  letter-spacing: 5px;
  position: relative;
  height: 6rem;
  width: 6rem;
}

.button__menu:hover span, .button__menu:hover span:before, .button__menu:hover span:after {
  box-shadow: 1px 1px 2px #000;
}

.button__menu span, .button__menu span:before, .button__menu span:after {
  display: block;
  height: 2px;
  width: 4rem;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -2rem;
  background-color: white;
}

.button__menu span:before {
  content: '';
  margin-top: -12px;
  transition: all 0.5s ease;
}

.button__menu span:after {
  content: '';
  margin-top: 10px;
  transition: all 0.5s ease;
}

.button__menu_mobile:hover span,
.button__menu_mobile:focus span,
.button__menu_mobile:active span {
  box-shadow: none;
}

.button__menu_mobile span:before {
  transform: rotate(135deg);
  margin-top: 0;
}

.button__menu_mobile span:after {
  transform: rotate(-135deg);
  margin-top: 0;
}

.b-header {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 768px) {
  .b-header {
    position: relative;
  }
}

.b-header .logo,
.b-header .main-menu,
.b-header .button {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  align-items: center;
}

@media screen and (max-width: 768px) {
  .b-header .logo {
    flex: 1;
    font-size: 2.8rem;
  }
}

@media screen and (max-width: 544px) {
  .b-header .logo {
    font-size: 3rem;
    flex-basis: 100%;
    justify-content: center;
    margin-bottom: 2rem;
  }
}

@media screen and (max-width: 544px) {
  .b-header {
    align-items: flex-start;
  }
}

.b-header .main-menu {
  flex-grow: 1;
  justify-content: flex-end;
}

@media screen and (max-width: 992px) {
  .b-header .main-menu {
    order: 1;
    margin-top: 2rem;
    justify-content: center;
  }
}

@media screen and (max-width: 544px) {
  .b-header .main-menu {
    display: none;
    order: 0;
    flex: 1;
    margin-top: 0;
  }
}

.b-header .main-menu__link:hover:after {
  border: 1px solid white;
  animation: widthBottomLine .3s;
}

.b-header .button__menu {
  display: none;
}

@media screen and (max-width: 544px) {
  .b-header .button__menu {
    display: block;
    order: 0;
  }
}

.logo {
  font-size: 3rem;
  font-weight: bold;
  letter-spacing: .2em;
  color: inherit;
}

.logo__title {
  margin-left: 2rem;
}

.main-menu__link {
  position: relative;
  display: block;
  padding: 1.5rem 1.9rem;
  font-size: 1.6rem;
  color: inherit;
}

@media screen and (max-width: 768px) {
  .main-menu__link {
    padding: 1.5rem 1.8rem;
  }
}

.main-menu__link:hover {
  text-shadow: 1px 1px 2px #000;
}

.main-menu__link:hover:after {
  position: absolute;
  content: '';
  display: block;
  bottom: 1px;
  width: 90%;
  left: 50%;
  transform: translateX(-50%);
}

@media screen and (max-width: 544px) {
  .main-menu {
    margin: 0;
    border-radius: 25px;
    background-color: rgba(110, 69, 226, 0.7);
    border: 1px solid white;
    z-index: 1;
  }
  .main-menu__item {
    display: block;
    width: 100%;
    text-align: center;
  }
  .main-menu__item:first-child .main-menu__link {
    border-radius: 25px 25px 0 0;
  }
  .main-menu__item:last-child .main-menu__link {
    border-radius: 0 0 25px 25px;
  }
  .main-menu__link {
    padding: 1.5rem 0;
  }
  .main-menu__link:hover {
    background-color: rgba(255, 255, 255, 0.2);
  }
  .main-menu__link:hover:after {
    content: none;
  }
}

.s-main {
  padding: 5rem 0;
  background-image: linear-gradient(-60deg, rgba(136, 211, 206, 0.85) 0%, rgba(110, 69, 226, 0.85) 100%), url(../images/bg-main.jpg);
  background-size: cover;
  background-position: center center;
  background-color: white;
}

@media screen and (max-width: 544px) {
  .s-main {
    padding: 3rem 0;
  }
}

.s-main .b-header {
  color: white;
}

.s-main .b-header .button {
  border: 1px solid white;
}

.s-main .b-header .button:hover {
  background-color: rgba(110, 69, 226, 0.7);
}

.s-main .b-header .button__menu span, .s-main .b-header .button__menu span:before, .s-main .b-header .button__menu span:after {
  background-color: white;
}

.s-main .b-header .button__menu_mobile span {
  background-color: transparent;
}

.s-main .section-content {
  color: white;
}

.s-main .section-content__h3:before, .s-main .section-content__h3:after {
  border-bottom: 1px solid white;
}

@media screen and (max-width: 544px) {
  .s-main .section-content {
    margin-top: 5rem;
  }
}

.s-main .arrow-down {
  margin-top: 18rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  align-self: center;
  width: 64px;
  height: 64px;
  background-color: white;
  border-radius: 50%;
  box-shadow: 0 0 18.7px 2.3px rgba(204, 204, 223, 0.5);
}
